A;CD0gCD;AC/gCD;EAOI,aAAA;EACA,0CAAA;UAAA,kCAAA;CD2gCH;ACvgCD;AACE;IAEI,aAAA;IACA,0CAAA;YAAA,kCAAA;CDwgCH;CACF;AClgCD;AACE;IAEI,0CAAA;YAAA,kCAAA;CDmgCH;CACF;AC7/BD;AACE;IAEI,0CAAA;YAAA,kCAAA;CD8/BH;CACF;ACv/BD;AACE;IAEI,0CAAA;YAAA,kCAAA;CDw/BH;CACF;ACn/BD;AACE;IAEI,0CAAA;YAAA,kCAAA;CDo/BH;CACF;AC/+BD;AACE;IAEI,0CAAA;YAAA,kCAAA;CDg/BH;CACF","file":"App.vue","sourcesContent":["@import \"theme.less\";\nul, li{\n  list-style-type: none;\n}\n/* 滑动 */\n.slide{\n  flex: 1;\n  overflow: auto;\n}\n/* 底部兑换按钮 */\n.exchange_but01{\n  bottom: 0px !important;\n}\n.exchange_but{\n  position: absolute;\n  bottom: 49px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  width: 100%;\n  height: 50px;\n  border-top: 1px solid @backgroundLightGrey;\n  border-bottom: 1px solid @backgroundLightGrey;\n  background-color: @backgroundWhite;\n  .but_text01{\n    padding: 0 5px 0 10px;\n    max-width: 200px;\n    text-align: center;\n    white-space: nowrap;\n    overflow: hidden;\n    b{font-size: 20px;color: @blueColor;}\n  }\n  .but_text02{\n    padding: 0 5px 0 10px;\n    text-align: center;\n    color: @greyColor;\n    max-width: 200px;\n    white-space: nowrap;\n    overflow: hidden;\n  }\n  .but{\n    margin: 0 10px 0 5px;\n    width: 100px;\n    height: 35px;\n    border-radius: 50px;\n    background-color: @blueColor;\n    border: none;\n    color: @backgroundWhite;\n    font-size: 16px;\n  }\n  .cart{\n    width: 90px;\n    height: 50px;\n    box-sizing: border-box;\n    padding: 0px 15px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: @blueColor;\n    position: relative;\n    .cart-icon{\n      color: @whiteColor;\n      font-size: 28px;\n    }\n    .badge{\n     position: absolute;\n     top: 8px;\n     left: 58%;\n    }\n    .van-badge{\n      background-color: @orangeColor;\n    border: none;\n    }\n  }\n}\n// 首页\n.index{\n  // overflow-x:hidden;\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n  position: relative;\n  /* 广告图 */\n  .van-swipe{\n    img{\n      width: 100%;\n      height: 150px;\n    }\n  }\n  /* 烟品 */\n.van_list{\n  margin-bottom: 100px;\n  margin-top: -3px;\n  .van-tabs__line{\n    background-color:@blueColor !important;\n  }\n  .van-tab--active{\n    color:@blueColor !important;\n  }\n  .van-stepper--round .van-stepper__plus{\n    background-color: @blueColor !important;\n  }\n  .van-stepper--round .van-stepper__minus {\n    color: @blueColor !important;\n    background-color: #fff;\n    border: 0.0625rem solid @blueColor !important;\n}\n  ul{\n    width: 95%;\n    margin: 10px auto auto auto;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: space-between;\n    li{\n      width: 173px;\n      height: 230px;\n      box-sizing: border-box;\n      background-color: @backgroundWhite;\n      margin-bottom: 10px;\n      border-radius: 5px;\n      overflow: hidden;\n      // padding: 8px;\n      position: relative;\n      .list-img{\n        display: block;\n        width: 100%;\n        height: 150px;\n        // margin: 8px auto; \n      }\n      h3{\n        width: 100%;\n        box-sizing: border-box;\n        margin: 5px auto;\n        word-break: break-all;\n        text-overflow: ellipsis;\n        display: -webkit-box;\n        -webkit-box-orient: vertical;\n        -webkit-line-clamp: 2;\n        overflow: hidden;\n        // text-align: center;\n        font-size: 14px;\n        padding: 0px 8px;\n      }\n      p{\n        width: 100%;\n        display: flex;\n        flex-wrap: nowrap;\n        align-items: center;\n        box-sizing: border-box;\n        padding: 0px 8px;\n        position: absolute;\n        bottom: 8px;\n        .price{\n          width: 55%;\n          overflow: hidden;\n          color: @blueColor;\n          font-size: 16px;\n          font-weight: 600;\n        }\n        .stepper_but{\n          width: 45%;\n          margin-left: 5px;\n          .van-stepper__input{\n            width: 1.5rem !important;\n          }\n        }\n      }\n    }\n  }\n}\n \n}\n\n/* 商家信息 */\n.information{\n  width: 100%;\n  overflow: hidden;\n  .head{\n    width: 100%;\n    height: 110px;\n    // margin-top: 10px;\n    background-color: @backgroundWhite;\n    display: flex;\n    align-items: center;\n    border-bottom: 1px solid @backgroundLightGrey;\n    img{\n      width: 80px;\n      height: 80px;\n      border-radius: 50px;\n      margin: 0 15px 0px 25px;\n    }\n    .head_con{\n      max-width: 60%;\n      line-height: 30px;\n      overflow: hidden;\n      .head_title{\n        font-size: 16px;\n        overflow: hidden;\n        text-overflow:ellipsis;\n        white-space: nowrap;\n      }\n      .head_km{\n        display: flex;\n        align-items: center;\n        font-size: 15px;\n      }\n    }\n  }\n  .business_address{\n    width: 100%;\n    padding: 15px 10px;\n    box-sizing: border-box;\n    background-color: @backgroundWhite;\n    display: flex;\n    flex-wrap: nowrap;\n    border-bottom: 1px solid @backgroundLightGrey;\n    .address_left{\n       width: 22%;\n    }\n    .address_cont{\n      width: 68%;\n    }\n    .address_right{\n      width: 10%;\n      text-align: center;\n      line-height: 40px;\n      color: @blueColor;\n      font-size: 30px;\n    }\n  }\n  .business_phone{\n    width: 100%;\n    padding: 10px 10px;\n    box-sizing: border-box;\n    background-color: @backgroundWhite;\n    border-bottom: 1px solid @backgroundLightGrey;\n    line-height: 35px;\n    .phone{\n      display: flex;\n      align-items: center;\n      flex-wrap: nowrap;\n      a{\n        width: 80%;\n        color: @blackColor;\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        .phone_right{\n           font-size: 30px;\n           color: @blueColor;\n        }\n      }\n      // .phone_left{\n      //   width: 20%;\n      // }\n      // .phone_cont{width: 70%;}\n   